.card {
  border: 1px solid #f8f8f8;
  padding: 20px;
  width: 220px;
  border-radius: 40px;
  margin-right: 30px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  &:hover {
    box-shadow: 0px 14px 30px rgba($color: #000000, $alpha: 0.06);
    transform: translateY(-5px);
  }
  .favorite {
    position: absolute;
    cursor: pointer;
  }
  span {
    opacity: 0.5;
    font-size: 14px;
    text-transform: uppercase;
  }
  b {
    font-size: 14px;
  }
  h5 {
    font-weight: 400;
    font-size: 14px;
  }
  .plus {
    margin: 0;
    padding: 0;
    background: rgb(255, 255, 255);
    border: 1px solid #e2e2e2;
    box-sizing: border-box;
    border-radius: 8px;
    height: 32px;
    width: 32px;
    cursor: pointer;
  }
  .img {
    width: 133px;
    height: 112px;
  }
}

@media (max-width: 850px) {
  .card {
    width: 200px;
    span {
      font-size: 12px;
    }
    b {
      font-size: 12px;
    }
    h5 {
      font-size: 12px;
    }
    .img {
      width: 120px;
      height: 101px;
    }
  }
}

